console.log("common");

$.ajax({url: "/hogehoge/hoge"})


function asyncFunction() {

    // Promise.resolve
    return new Promise(function (resolve, reject) {
        setTimeout(function () {
            // 
            resolve('Async Hello world');
        }, 1000);
    });
}

asyncFunction().then(function (value) {
    // 
    console.log(value);    // => 'Async Hello world'
}).catch(function (error) {
    // 
    console.log(error);
});

console.log("OK");

hoge();
